import React from "react";

function RightCard(props) {
  return (
    <section className="py-4 pr-4 my-10 flex flex-col items-stretch place-items-center justify-center border-2 rounded-xl md:flex-row">
      <div className="relative w-full flex-grow sm:w-1/2 pb-16">
        <div>
          <div className="font-bold text-3xl ml-4">
            {props.title}
          </div>
          <div className="font-semibold text-lg ml-4">
            {props.subheader}
          </div>
          <div className="mx-2 my-4 sgrid grid-cols-3 sm:flex">
          {
            props.technology.map((aTechnology,index) => {
              return (
                <span className="md:m-2  rounded-full border-2 w-min p-2 mx-2" key={index}>
                  {aTechnology}
                </span >
              );
            })
          }
          </div>

          <div className="ml-4 mr-4">
            {props.description}
          </div>
          <div className="ml-4">
            {props.demoLink}
          </div>
        </div>
        <div className="flex absolute bottom-0 left-0 ">
          <div className="my-4 items-end">
            <a className="place-content-end content-end ml-4 text-blue-400 rounded-full border-2 px-4 py-2" href= {props.githubLink}>
              GitHub Link
            </a>
          </div>
        </div>
      </div>
      
      <div className="sm:w-1/2 flex items-stretch m-4">
      {
        props.images.map((anImage, index) => {
          return (
            <img className="self-stretch" src={anImage} alt="missing RightCard" key={index}></img>
          );
        })
      }
      </div>
    </section>
  );
}

export default RightCard;
